<template>
    <div>
        <h2>
           库存统计图
        </h2>
        
        <div class="chart" id="myEchartsFour">
            
        </div>
        
    </div>
</template>

<script>
import {inject,reactive,onMounted} from "vue"
export default{
    setup(){
        let $echarts=inject("echarts")
        let $http=inject("axios")

        let data=reactive({})
        
        async function getStyle(){
            data=await $http({url:"/four/data"})
        }
        onMounted(()=>{
            
            getStyle().then(()=>{
                console.log("统计图4",data.data.chartData.chartData)
            let myChart=$echarts.init(document.getElementById("myEchartsFour"))    
                myChart.setOption({
                    grid:{
                        left:"2%",
                        right:"4%",
                        bottom:"4%",
                        containLabel:true
                    },
                    xAxis:{
                        type:"category",
                        data:data.data.chartData.chartData.day,
                        
                        axisLine:{
                            lineStyle:{
                                color:"#fff"
                            }
                        }
                    },
                    yAxis:{
                        type:"value",
                        axisLine:{
                            lineStyle:{
                                color:"#fff"
                            }
                        }
                    },
                    legend:{
                        top:"3%"
                    },
                    tooltip:{
                        trigger:"axis",
                        axisPointer:{
                            tyle:"shadow"
                        }
                    },
                    series:[
                        {
                            name:"服饰",
                            type:"bar",
                            stack:"total",  //堆叠效果
                            label:{
                                show:true
                            },
                            emphasis:{
                              focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.Chemicals
                        },
                        {
                            name:"数码",
                            type:"bar",
                            stack:"total",  //堆叠效果
                            label:{
                                show:true
                            },
                            emphasis:{
                              focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.gear
                        },
                        {
                            name:"家电",
                            type:"bar",
                            stack:"total",  //堆叠效果
                            label:{
                                show:true
                            },
                            emphasis:{
                              focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.Electrical
                        },
                        {
                            name:"家具",
                            type:"bar",
                            stack:"total",  //堆叠效果
                            label:{
                                show:true
                            },
                            emphasis:{
                              focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.digit
                        },
                        {
                            name:"日化",
                            type:"bar",
                            stack:"total",  //堆叠效果
                            label:{
                                show:true
                            },
                            emphasis:{
                              focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.Clothes
                        },
                    ]
                })
            })
        })
        return{
            getStyle,data
        }
    }
}
</script>

<style>
/* .myEchartFour{
left:80%
} */
</style>
