<template>
    <div>
        <h2>
           库存统计
        </h2>
        
        <div class="chart" id="myEcharts">
        
        </div>
        
    </div>
</template>

<script>
import {inject,onMounted,reactive} from "vue"
export default{
    setup(){
        let $echarts = inject ("echarts")
        let $http = inject("axios")

        let data = reactive({})
        async function getStyle(){
            data=await $http({url:"/three/data"})
        }
        onMounted(()=>{
            getStyle().then(()=>{
                console.log("饼状图",data.data.chartData.chartData)
                let myChart = $echarts.init(document.getElementById("myEcharts"))
                myChart.setOption({
                    legend:{
                        top:"bottom"
                    },
                    tooltip:{
                        show:true
                    },
                    series:[
                        {
                            type:"pie",
                            data:data.data.chartData.chartData,
                            radius:[10,100],   //内径，外镜
                            center:["50%","45%"],
                            roseType:"area",  //玫瑰图
                            itemStyle:{
                                borderRadius:10   //扇形圆角
                            }
                        }
                    ]

                })
            })
        })
        return{
            getStyle,data
        }
    }
}
</script>

<style>
.chart{
    height:19rem;
}
h2{
    height:1rem;
    color:#fff;
    line-height:1.5rem;
    font-size:.9rem;
    text-align:center;
}
</style>
