<template>
    <div class="chart" >
        <h2>
           日单量
        </h2>

        <div class="chart" id="oneChart">
    
        </div>
        
    </div>
</template>

<script>
import {inject,onMounted,reactive} from "vue"

export default{
    setup(){
        let $echarts=inject("echarts")
        let $http=inject("axios")

        let data=reactive({})
        let xdata=reactive([])
        let ydata=reactive([])
        
        function setData(){
            xdata = data.data.chartData.chartData.map(v=>v.title)
            ydata = data.data.chartData.chartData.map(v=>v.num)
            console.log("xData",xdata)
            console.log("yData",ydata)
        }

        async function getStyle(){   //创建一个方法
            data = await $http({url:"/one/data"})
            // console.log(oneData.data.chartData.chartData)
        }
        
        onMounted(()=>{
            let myChart=$echarts.init(document.getElementById("oneChart"))   //初化图表
            //调用请求
            getStyle().then(()=>{
                setData()
                
            myChart.setOption({
                grid:{
                    top:"7%",
                    left:"1%",
                    right:"6%",
                    bottom:"3%",
                    containLabel:true   //包含坐标文字 

                },
                xAxis:{
                    type:"value",
                    axisLine:{
                        lineStyle:{
                            color:"#fff"
                        }
                    }
                },
                yAxis:{
                    type:"category",
                    data:xdata,
                    axisLine:{
                        lineStyle:{
                            color:"#fff"
                        }
                    }
                },
                series:[
                    {
                        data:ydata,
                        type:"bar",
                        
                        itemStyle:{
                            normal:{
                                barBorderRadius:[0,20,20,0],   //圆角弧度
                                color:new $echarts.graphic.LinearGradient(0,0,1,0,[
                                    {
                                        offset:0,
                                        color:"#005eaa"
                                    },
                                    {
                                        offset:0.5,
                                        color:"#339ca8"
                                    },
                                    {
                                        offset:1,
                                        color:"#cda819"
                                    },
                                ])
                            }
                        }
                    }
                ]
            })
        
            })
            
        })  
        return{
            getStyle,data,xdata,ydata
        }
    }
}
</script>

<style scoped>
.chart{
    height: 18rem;
}
h2{
    height:1rem;
    color:#fff;
    line-height:1.5rem;
    font-size:.9rem;
    text-align:center;
}
</style>
