<template>
    <div class="map" id='map'>
    </div>
    </template>
    <script>
    import axios from "axios"
    // 1.引用
    import {onMounted,reactive,inject} from "vue"

export default {
    setup(){
        // 2.得到echarts
        let $echarts= inject("echarts")
        let $http=inject("axios")
        let mapData=reactive({})

        
        async function getState(){
            mapData=await $http({url:"/china/data"})
        }
        onMounted(()=>{
            getState().then(()=>{
                console.log("map",mapData.data.chartData)
                // 3.设置地图
                $echarts.registerMap("china",mapData.data.chartData);
                var chart = $echarts.init(document.getElementById("map"));
                chart.setOption({
                    geo: {
                        // type:"map",
                        map: 'china',
                        itemStyle:{
                            areaColor:"#0099ff",   //填充
                            borderColor:"#00ffff",  //描边
                            shadowColor:"rgba(230,130,70,.5)",  //阴影
                            shadowBlur:30,
                            emphasis:{
                                focus:"self"   //设置当前内容高亮
                            }
                        }
                    },                    
                    tooltip:{
                        trigger:"item",  //触发提示
                    },
                    title:{
                        text:"城市销量",
                        left:"43%",
                        top:"3%",
                        textStyle:{
                            color:"#fff",
                            fontSize:20,
                            textShadowBlur:10,
                            textShadowColor:"#33ffff"
                        }
                    },
                    visualMap:{
                        type:"continuous",
                        min:100,
                        max:5000,
                        calculable:true,  //滑动效果
                        inRange:{
                            color:["#50a3ba","#eac736","#d94e5d"]
                        }
                    },
                    textStyle:{
                        color:"#fff",
                    },
                    series:[
                        {
                            type:"scatter",  //散点图对象
                            itemStyle:{
                                color:"red",
                            },
                            type:"effectScatter",  //涟漪效果
                            rippleEffect: {
                                //涟漪特效相关配置。
                                number: 2, //波纹的数量。
                                scale: 4, //动画中波纹的最大缩放比例
                            },
                            coordinateSystem:"geo",
                            data:[
                                {name:"北京",value:[116.46,39.92,4367]},
                                {name:"上海",value:[121.48,31.22,8675]},
                                {name:"深圳",value:[114.07,22.62,2461]},
                                {name:"广州",value:[113.23,23.16,187]},
                                {name:"西安",value:[108.45,34,3421]}
                            ],
                        },
                    ],
                });
            })
        })
        return {
            mapData
        }
    }
}
</script>

<style lang="less">
    .map{
    width: 100%;
    height: 100%;
}
</style>
