<template>
    <div>
        <h2>
            周销图
        </h2>
        
        <div class="chart" id="myEchartsTwo">
        </div>
        
    </div>
</template>

<script>
import {inject,reactive,onMounted} from "vue"
export default{
    setup(){
        let $echarts=inject("echarts")
        let $http=inject("axios")
        let data=reactive({})
        async function getState(){
            data=await $http({url:"/two/data"})
        }
        onMounted(()=>{
            getState().then(()=>{
                console.log("折线图",data.data.chartData)

                let myChart=$echarts.init(document.getElementById("myEchartsTwo"))
                myChart.setOption({
                    tooltip:{
                        trigger:"axis",
                        axisPointer:{
                            type:"cross",
                            label:{
                                backgroundColor:"#e6b600"  //十字准星提示框数据填充颜色
                            }
                        }
                    },
                    // 图例设置
                    legend:{
                        top:"3%",
                        data:["服饰","数码","家电","家具","日化"]
                    },
                    grid:{
                        left:"1%",
                        right:"8%",
                        bottom:"3%",
                        containLabel:true,
                    },
                    xAxis:{
                        type:"category",
                        boundaryGap:false,   //设置两端数据没有间隙
                        data:data.data.chartData.chartData.day,
                        axisLine:{
                            lineStyle:{
                                color:"#fff"
                            }
                        }
                    },
                    yAxis:{
                        type:"value",
                        axisLine:{
                            lineStyle:{
                                color:"#fff"
                            }
                        }
                    },
                    series:[
                        {
                            name:"服饰",
                            type:"line",
                            smooth:true,
                            stack:"num",
                            showSymbol:false,
                            axisLine:{
                            areaStyle:{
                                color: new $echarts.graphic.LinearGradient(0, 0, 0,1,
                                [
                                    {
                                    offset: 0,
                                    color: "rgb(128, 255, 165)",
                                    },
                                    {
                                    offset: 1,
                                    color: "rgb(1, 191, 236)",
                                    },
                                ])
                            }
                            },
                            emphasis:{
                                focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.Chemicals
                        },
                    
                        {
                            name:"数码",
                            type:"line",
                            smooth:true,
                            stack:"num",
                            showSymbol:false,
                            areaStyle:{
                                opacity: 0.8,
                                color: new $echarts.graphic.LinearGradient(0, 0, 0,1,
                                [
                                    {
                                    offset: 0,
                                    color: "rgb(0, 221, 255)",
                                    },
                                    {
                                    offset: 1,
                                    color: "rgb(77, 119, 255)",
                                    },
                                ]),
                            },
                            lineStyle: { // 设置线段样式
                                width: 0,
                            },
                            emphasis:{
                                focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.Clothes
                        },
                        {
                            name:"家电",
                            type:"line",
                            smooth:true,
                            stack:"num",
                            showSymbol:false,
                            areaStyle:{
                                opacity: 0.8,
                                color: new $echarts.graphic.LinearGradient(0, 0, 0,1,
                                [
                                    {
                                    offset: 0,
                                    color: "rgb(55, 162, 255)",
                                    },
                                    {
                                    offset: 1,
                                    color: "rgb(116, 21, 219)",
                                    },
                                ]),
                            },
                            lineStyle: { // 设置线段样式
                                width: 0,
                            },
                            emphasis:{
                                focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.Electrical
                        },
                        {
                            name:"家具",
                            type:"line",
                            smooth:true,
                            stack:"num",
                            showSymbol:false,
                            areaStyle:{
                                opacity: 0.8,
                                color: new $echarts.graphic.LinearGradient(0, 0, 0,1, 
                                [
                                    {
                                    offset: 0,
                                    color: "rgb(255, 0, 135)",
                                    },
                                    {
                                    offset: 1,
                                    color: "rgb(135, 0, 157)",
                                    },
                                ]),
                            },
                            lineStyle: { // 设置线段样式
                                width: 0,
                            },
                            emphasis:{
                                focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.digit
                        },
                        {
                            name:"日化",
                            type:"line",
                            smooth:true,
                            stack:"num",
                            showSymbol:false,
                            areaStyle:{
                                opacity: 0.8,
                                color: new $echarts.graphic.LinearGradient(0, 0, 0,1, 
                                [
                                    {
                                    offset: 0,
                                    color: "rgb(255, 191, 0)",
                                    },
                                    {
                                    offset: 1,
                                    color: "rgb(224, 62, 76)",
                                    },
                                ]),
                            },
                            lineStyle: { // 设置线段样式
                                width: 0,
                            },
                            emphasis:{
                                focus:"series"  
                            },
                            data:data.data.chartData.chartData.num.gear
                        },
                    ]
                })
            })
        })
        return{
            getState,data
        }
    }
}
</script>

<style>
